import { SET_LANGUAGE } from 'redux/types';

const mainReducedInit = {
  language: 'Eng',
};

const mainReducer = (state = mainReducedInit, action) => {
  switch (action.type) {
    case SET_LANGUAGE:
      return { ...state, language: action.payload };

    default:
      return state;
  }
};

export default mainReducer;
